import React from 'react'
import styled from 'styled-components'

const Prize = ({ prize, openModal, stock }) => {
  const url = prize ? `https:${prize.fields.img.fields.file.url}` : stock;

  return (
    <Container style={{ opacity: prize ? 1 : .3 }}>
      <Image url={url} />
      <Content>
        <h1>{prize ? prize.fields.name : 'Loading'}</h1>
        <Button type="button" onClick={openModal}>Enter raffle</Button>
      </Content>
    </Container>
  )
}

export default Prize;

const Image = styled.div`
  background-image: ${props => `url(${props.url})`};
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 120px;
`; 

const Container = styled.div`
  background: #303841;
  margin: 10px;
  border-radius: 5px;
  overflow: hidden;
  width: 265px;
  transform: translateY(0);
  box-shadow: 0 14px 28px rgba(0,0,0,0), 0 10px 10px rgba(0,0,0,0);
  transition: transform .2s ease-in-out, box-shadow .2s ease-in-out;

  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  }
`;

const Content = styled.div`
  padding: 20px;
  text-align: center;

  h1 {
    font-size: 25px;
  }
`;

const Button = styled.button`
  background: #d62246;
  border: none;
  border-radius: 3px;
  padding: 5px 20px;
  color: white;
  width: 100%;
  outline: none;
  transform: translateX(0);
`;
