import React from 'react'
import styled from 'styled-components'
import { ContentfulApi } from '../client'
import StockImage from '../images/300x150.png'

import Prize from './prize'
import Modal from './modal'

const lazyLoad = [1, 2, 3, 4, 5, 6]

class Raffle extends React.Component {
  _isMounted = false;
  state = {
    prizes: [],
    loaded: false,
    modalOpen: false,
  }
  async componentDidMount() {
    this._isMounted = true;
    try {
      const prizes = await ContentfulApi.getEntries();
      this.setState({
        prizes,
        loaded: true,
      });
    } catch (err) {
      console.log(err);
      this.setState({ loaded: true });
    }
  }
  openModal = () => {
    this.setState({ modalOpen: true })
  }
  closeModal = () => {
    this.setState({ modalOpen: false })
  }
  componentWillUnmount(){
    this._isMounted = false;
  }
  render() {
    const { prizes, loaded, modalOpen } = this.state;
    return (
      <Container>
        { loaded ? 
          prizes.map((prize, i) => (
            <Prize key={prize.sys.id} prize={prize} openModal={this.openModal} />
          ))
            : 
            lazyLoad.map((prize, i) => (
              <Prize key={i} stock={StockImage} />
            ))
        }
        <Modal open={modalOpen} close={this.closeModal} />
      </Container>
    );
  }
}

export default Raffle;

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
  align-items: left;
  justify-content: space-between;
`;
