import React from 'react'
import styled from 'styled-components'

import Downloads from './downloads'

const Modal = ({ open, close }) => (
  <Container className={open ? 'open' : ''} onClose={close}>
    <ModalContainer>
      <Title>
        Download the app to enter
        <CloseButton onClick={close}></CloseButton>
      </Title>
      <Content>
        <Downloads />
      </Content>
    </ModalContainer>
    <Overlay onClick={close} />
  </Container>
)

export default Modal;

const Container = styled.div`
  position: absolute;
`;

const ModalContainer = styled.div`
  position: fixed;
  bottom: -100px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 600px;
  opacity: 0;
  visibility: hidden;
  background-color: #303841;;
  z-index: 3;
  transition: opacity .2s ease-in-out, bottom .3s ease-out;

  .open & {
    opacity: 1;
    visibility: visible;
    bottom: 0;
  }
`;

const Title = styled.h2`
  position: relative;
  padding: 15px 40px;
  font-weight: 700;
  color: white;
  background-color: #303841;;
`;

const CloseButton = styled.span`
  position: absolute;
  right: 10px;
  top: 7px;
  width: 32px;
  height: 32px;
  opacity: 0.5;

  &:hover {
    opacity: 1;
  }
  &:before, 
  &:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: white;
  }
  &:before {
    transform: rotate(45deg);
  }
  &:after {
    transform: rotate(-45deg);
  }
`;

const Content = styled.div`
  padding: 20px;
  line-height: 1.2em;
`;

const Overlay = styled.div`
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: black;
  opacity: 0;
  visibility: hidden;
  z-index: 2;
  transition: opacity .2s;
  
  .open & {
    opacity: .7;
    visibility: visible;
  }
`;
