import { createClient } from 'contentful';

class ContentfulApi {
  client = null;

  constructor() {
    this.client = createClient({
      space: 'c13o8hijevsb',
      accessToken:
        'f1b57c64b1770972b6f9d9cee52e4e934d77a849fb34f710ab12d96f10cad5de',
    });
  }

  getEntries = async () => {
    try {
      const entry = await this.client.getEntries();
      const { items } = entry;
      return items;
    } catch (err) {
      return [];
    }
  };
}

export default new ContentfulApi();
